.block-about .block__title {
  text-align: center;
  display: block;
}

.block-about h2 {
  text-align: center;
  display: block;
  margin-bottom: calc(80px + 72px);
}

.block-about .block__content {
  display: flex;
}

.block-about .block__link {
  margin-top: 40px;
  display: flex;
  flex: 1 100%;
}

.block-about .block__link a {
  margin: 0 auto;
}

.block-about .block__items {
  display: grid;
  grid-gap: 40px 0;
  grid-template-columns: 1fr 1fr 1fr;
}

.block-about .item-about__title {
  font-size: 54px;
  color: #333333;
  font-weight: 700;
}

.block-about .item-about__description {
  text-align: center;
  color: #333333;
  margin-top: -14px;
}

.block-about .item-about {
  display: flex;
  flex-flow: column wrap;
  align-items: center;
}

.block-about-text .block__content {
  display: grid;
  font-weight: 500;
  grid-gap: 0 45px;
  grid-template-columns: 1fr 2fr;
  align-items: center;
}

.block-about-text .block__img {
  display: flex;
}

.block-about-text p {
  margin: 0;
  order: -1;
}

.block-about-text p:first-child {
  margin-top: 0;
}

.block-about-text p:last-child {
  margin-bottom: 0;
}

.block-about-text .block__text {
  line-height: 32px;
}

@media only screen and (min-width: 768px) {
  .block-about .block__items {
    grid-gap: 70px 0;
  }

  .block-about .item-about__image {
    position: absolute;
  }

  .block-about .item-about:nth-child(2) .item-about__image {
    position: absolute;
    top: -72px;
  }

  .block-about-text .block__img {
    grid-row: 1 / 3;
  }
}

@media only screen and (min-width: 1024px) {
  .block-about .item-about__image {
    top: 0px;
  }
}

@media only screen and (max-width: 1279px) {
  .block-about-text .block__text {
    line-height: 28px;
  }
}

@media only screen and (max-width: 1023px) {
  .block-about .item-about__title {
    font-size: 43px;
  }

  .block-about .item-about__description {
    font-size: 15px;
    line-height: 23px;
  }

  .block-about-text .block__text {
    line-height: 26px;
  }
}

@media only screen and (max-width: 767px) {
  .block-about h2 {
    margin-bottom: 50px;
    line-height: 28px;
  }

  .block-about .block__items {
    display: flex;
    flex-flow: column nowrap;
    font-size: 60px;
    justify-content: center;
    width: 100%;
  }

  .block-about .item-about__description {
    font-size: 15px;
  }

  .block-about .item-about.item-big {
    min-height: 3.5em;
    margin: 0.6em 0 0.5em;
  }

  .block-about .item-about.item-big .item-about__image {
    max-height: 1.9em;
  }

  .block-about .item-about.item-big .item-about__title {
    top: 1em;
  }

  .block-about .item-about.item-big .item-about__description {
    font-size: .36em;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    top: 7.2em;
    line-height: 1.2;
    max-width: 18em;
    margin: 0 auto;
  }

  .block-about .item-about {
    justify-self: center;
    height: 2.2em;
    width: 100%;
  }

  .block-about .item-about .item-about__title {
    font-size: 1em;
    position: absolute;
    right: 0;
    width: 100%;
    text-align: center;
    height: 100%;
    bottom: 0;
  }

  .block-about .item-about .item-about__description {
    font-size: 0.36em;
    position: absolute;
    bottom: 0;
    width: 20em;
    left: 0;
    right: 0;
    width: 100%;
    top: 5em;
  }

  .block-about .item-about .item-about__image {
    width: 100%;
    height: 100%;
    max-width: initial;
  }

  .block-about .item-about .item-about__image svg {
    position: absolute;
    top: 0;
    height: 100%;
    display: block;
    margin: 0 auto;
    left: 0;
    right: 0;
  }

  .block-about .item-about .item-about:nth-child(2) .item-about__image {
    position: absolute;
    top: -72px;
  }

  .block-about-text .block__content {
    grid-template-columns: 1fr;
  }

  .block-about-text .block__img img {
    width: 100%;
    max-width: 400px;
    margin: auto;
  }

  .block-about-text p {
    margin: 20px 0;
  }

  .block-about-text p:first-child {
    order: -1;
  }

  .block-about-text p:last-child {
    order: 1;
  }

  .block-about-text .block__text {
    line-height: 22px;
  }
}

@media only screen and (max-width: 600px) {
  .block-about .block__items {
    grid-template-columns: 1fr;
    font-size: 10vw;
  }
}